<script>
import api from '@/api'
import { getToken } from '@/api/auth'

export default {
  data () {
    return {
      self: this,
      columns: [
          {
              title: 'ID',
              key: 'id',
              align: 'center',
          },
          {
              title: '位置名称',
              key: 'group',
              align: 'center',
          },
          {
              title: '广告类型',
              key: 'adtype_name',
              align: 'center',
          },
          {
              title: '内容数量',
              key: 'advcount',
              align: 'center',
          },
          {
              title: '显示数量',
              key: 'num',
              align: 'center',
          },
          {
            title: '操作',
            key: 'action',
            align: 'center',
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  domProps: { // 修改标签默认内容
                    innerHTML: "编辑"
                  },
                  props: {
                    type: 'primary',
                    size: 'small',
                  },
                  style: {
                    marginRight: '5px'
                  },
                  on: {
                    click: () => {
                      this.gotoSiteedit(params)
                    }
                  }
                }, 'View'),
                h('Button', {
                  domProps: { // 修改标签默认内容
                    innerHTML: "内容设置"
                  },
                  props: {
                      type: 'success',
                      size: 'small'
                  },
                  on: {
                      click: () => {
                        this.gotoAdvertSet(params)
                      }
                  }
                }, 'Delete')
              ]);
            }
          }
      ],
      data: [],
      isLoading: true,
      totalPages: 0,
      params: {},
      modal_loading: false,
    }
  },
  mounted: function() {
    const token = getToken(),
          params = {"token": token, "page": "1", "per_page": "10", "need_menu":1};

    this.params = params;
    this.getData(params);
  },
  methods: {
    getData (params) {
      api.advert(params).then(res => {
        if(res.code === 0) {
          this.data = res.data.article
          this.$store.commit('SET_MENUSECOND', res.data.menu)
          this.totalPages = res.data.totlerows
          this.isLoading = false
        } else {
          this.$Notice.error({title: res.msg})
        }
        // console.log(res)
      }).catch(err => {
        this.$Notice.error({title: err})
      })
    },
    gotoAdvertSet (params) {
      const adid = params.row.id
      this.$router.push({
        path: '/ad/advertset',
        query: {"adid": adid}
      })
    },
    gotoSiteedit (params) {
      console.log(params)
      const aid = params.row.id
      this.$router.push({
        path: '/ad/siteedit',
        query: {"aid": aid}
      })
    },
    changePage: function(page) {
      // console.log(page)
      this.params.page = page;
      this.getData(this.params);
    },
    changePageNum: function(num) {
      // console.log(num)
      this.params.per_page = num;
      this.getData(this.params);
    },
  }
}
</script>
<template>
  <div>
    <h3><Icon type="md-list" size=24 />广告位列表</h3>
    <div class="searchBar">
      <Button type="primary" to="/ad/siteadd">新增广告位</Button>
    </div>
    <Table border :context="self" :columns="columns" :data="data" :loading=isLoading></Table>
    <div style="margin: 10px;overflow: hidden">
      <div style="float: right;">
        <Page :total="totalPages*1" show-total show-elevator show-sizer :page-size=10 @on-page-size-change="changePageNum" @on-change="changePage" />
      </div>
    </div>
  </div>
    
</template>
<style lang="scss" scoped>
  h3 {
    margin-bottom: 10px;
  }
  .searchBar {
    margin-bottom: 20px;
  }
</style>
